import clsx from "clsx";
import React from "react";

type Props = {
  text?: string;
  bounceIn?: "left" | "right" | "up" | "down";
};
const RubberText: React.FC<Props> = ({ text = "", bounceIn }) => {
  return (
    <div
      className={clsx("flex flex-row mr-8", {
        "animate-bounceInLeft": bounceIn === "left",
        bounceInRight: bounceIn === "right",
        bounceInUp: bounceIn === "up",
        bounceInDown: bounceIn === "down",
      })}
    >
      {text.split("").map((c, i) => (
        <h1
          key={i}
          className={clsx(
            "font-virgil text-7xl hover:animate-rubber hover:text-lime-400 cursor-pointer"
          )}
        >
          {c}
        </h1>
      ))}
    </div>
  );
};

export default RubberText;
